<template>
  <tabs :tabTitles="tabs" class="pt-header">
    <infinite @get-more="getTopics" v-for="i in tabs.length" :list="getList(i)" ref="infinite"></infinite>
  </tabs>
</template>

<script>
import tabs from 'components/Tabs';
import list from 'components/List';
import infinite from 'components/Infinite';
import { mapState, mapMutations } from 'vuex';
import { STARTAJAX, ENDAJAX } from 'store/types';

export default {
  name: 'app',
  components: {
    tabs,
    list,
    infinite
  },
  data() {
    return {
      tabs: ['', '', '', '', ''],
      all: {
        list: [],
        loaded: false,
        nextPage: 1,
        end: false,
        scrollTop: 0
      },
      good: {
        list: [],
        loaded: false,
        nextPage: 1,
        end: false,
        scrollTop: 0
      },
      share: {
        list: [],
        loaded: false,
        nextPage: 1,
        end: false,
        scrollTop: 0
      },
      ask: {
        list: [],
        loaded: false,
        nextPage: 1,
        end: false,
        scrollTop: 0
      },
      job: {
        list: [],
        loaded: false,
        nextPage: 1,
        end: false,
        scrollTop: 0
      }
    };
  },
  // scrollTop
  beforeRouteLeave(to, from, next) {
    this.setScrollTop(to);
    next();
  },
  // 
  created() {
    this.$root.$on('scroll-to-top', this.scrollToTop.bind(this));
  },
  computed: {
    // currentType of string
    // 
    currentType() {
      return this.getType(this.page);
    },
    ...mapState({
      page: state => state.tabs.page,
      ajaxing: state => state.ajax.ajaxing
    })
  },
  watch: {
    // 
    $route(to) {
      this.setScrollTop(to, true);
    },
    // getTopics when toggle tabs
    // tabstopics
    currentType(type) {
      if (!this[type].loaded) {
        this.getTopics();
      }
    }
  },
  mounted() {
    this.getTopics();
  },
  methods: {
    // pass a number(1-5),return the corresponding type of string
    // (15),
    getType(i) {
      let type = {
        1: 'all',
        2: 'good',
        3: 'share',
        4: 'ask',
        5: 'job'
      };
      return type[i];
    },
    // get topics list from vue.data
    // vuedata
    getList(i) {
      return this[this.getType(i)].list;
    },
    // get currentType's topics data from http request
    // http
    getTopics() {
      // return when ajaxing is true
      // ajax
      if (this.ajaxing) {
        return;
      }

      // save type,use for end of ajax
      // typethis.currentType
      let type = this.currentType;

      // currentType is not have data
      // 
      if (this[type].end) {
        return;
      }

      this.$http.get('topics', {
        params: {
          tab: type,
          page: this[type].nextPage,
          limit: 20
        },
        timeout: 5000,
        before(request) {
          this.startAjax(request);
        }
      }).then((response) => {
        this.endAjax();

        // filter the data that not author info
        // 
        let data = response.body.data.filter((item) => {
          return Object.keys(item.author).length;
        });

        // it means this type loaded
        // 
        this[type].loaded = true;

        // if don't have any data,set the end = true
        // endtrue
        if (!data.length || data.length < 20) {
          this[type].end = true;
        }

        // push data and increment nextPage
        // nextPage
        this[type].list.push(...data);
        this[type].nextPage++;
      }, (response) => {
        this.endAjax();
        window.alert('');
      });
    },
    // 
    setScrollTop(to, $route) {
      let infinite = this.$refs.infinite;
      let tabs = ['all', 'good', 'share', 'ask', 'job'];
      for (let i = 0; i < this.tabs.length; i++) {
        if (to.name === 'home') {
          // 
          infinite[i]['$el'].scrollTop = this[tabs[i]].scrollTop;
        } else if (!$route) {
          // 
          this[tabs[i]].scrollTop = infinite[i]['$el'].scrollTop;
        }
      }
    },
    // 
    scrollToTop() {
      let tabs = ['all', 'good', 'share', 'ask', 'job'];
      this.$refs.infinite[tabs.indexOf(this.currentType)]['$el'].scrollTop = 0;
    },
    ...mapMutations({
      startAjax: STARTAJAX,
      endAjax: ENDAJAX
    })
  }
};
</script>

<style lang="less">
</style>



