import { SETUSER, SETMESSAGE } from 'store/types';

export default {
  methods: {
    // get item tag
    // 
    getTag(top, good, tab) {
      if (top) {
        return '';
      }
      if (good) {
        return '';
      }
      let tags = {
        share: '',
        ask: '',
        job: ''
      };
      return tags[tab];
    },
    // getUserInfo
    // 
    getUserInfo() {
      this.$http.get('user/' + this.user.loginname).then(({body}) => {
        if (body.success) {
          this.$store.commit(SETUSER, {
            avatar_url: body.data.avatar_url,
            create_at: body.data.create_at,
            score: body.data.score,
            recent_replies: body.data.recent_replies,
            recent_topics: body.data.recent_topics
          });
        }
      }, ({body}) => {
        console.log(body.error_msg);
      });
    },
    // 
    getMessageCount() {
      this.$http.get('message/count', {
        params: {
          'accesstoken': this.user.token
        }
      }).then(({body}) => {
        if (body.success) {
          this.$store.commit(SETMESSAGE, body.data > 0);
        }
      }, ({body}) => {
        console.log(body.error_msg);
      });
    },
    // go to login
    // 
    goToLogin() {
      window.alert('');
      this.$router.push('/login');
    },
    // get token
    // token
    getToken() {
      return window.localStorage.getItem('token') || window.sessionStorage.getItem('token');
    },
    // 
    scrollToTop() {
      document.body.scrollTop = 0;
    },
    // @markdown
    linkUsers(text) {
      let users = this.fetchUsers(text);
      for (let i = 0, l = users.length; i < l; i++) {
        let name = users[i];
        text = text.replace(new RegExp('@' + name + '\\b(?!\\])', 'g'), '[@' + name + '](#/user/' + name + ')');
      }
      return text;
    },
    // (@xxx)
    fetchUsers(text) {
      if (!text) {
        return [];
      }

      let ignoreRegexs = [
        /```.+?```/g, //  ```
        /^```[\s\S]+?^```/gm, // ```  pre 
        /`[\s\S]+?`/g, // `some code` 
        // /^.*/gm, // 4 pre  . 
        /\b\S*?@[^\s]*?\..+?\b/g, // somebody@gmail.com 
        /[@.+?]\(\/.+?\)/g //  link  username
      ];

      ignoreRegexs.forEach((ignoreRegex) => {
        text = text.replace(ignoreRegex, '');
      });

      let results = text.match(/@[a-z0-9\-_]+\b/igm);
      let names = [];
      if (results) {
        for (let i = 0, l = results.length; i < l; i++) {
          let s = results[i];
          // remove leading char @
          s = s.slice(1);
          names.push(s);
        }
      }
      names = this.uniqueArr(names);
      return names;
    },
    // 
    uniqueArr(arr) {
      if (!this.isType(arr, 'Array')) {
        return [];
      }
      let set = new Set(arr);
      return Array.from(set);
    },
    // 
    isType(obj, type) {
      // types : ["Array", "Boolean", "Date", "Number", "Object", "RegExp", "String", "Window", "HTMLDocument"]
      return Object.prototype.toString.call(obj) === '[object ' + type + ']';
    }
  }
};



